.tippy-tooltip.joji-theme {
  background-color: #ffa4f6;
  width: 300px;
  color: #1b1723;
  text-align: left;
  padding: 20px;
  border-radius: 20px;
  font-family: museo-sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.tippy-tooltip.joji-theme[data-animatefill] {
  background-color: transparent;
}

.tippy-tooltip.joji-theme .tippy-backdrop {
  background-color: #ffa4f6;
}

.tippy-popper[x-placement^="top"] .tippy-tooltip.joji-theme .tippy-arrow {
  border-top-color: #ffa4f6;
}

.tippy-popper[x-placement^="bottom"] .tippy-tooltip.joji-theme .tippy-arrow {
  border-bottom-color: #ffa4f6;
}

.tippy-popper[x-placement^="left"] .tippy-tooltip.joji-theme .tippy-arrow {
  border-left-color: #ffa4f6;
}

.tippy-popper[x-placement^="right"] .tippy-tooltip.joji-theme .tippy-arrow {
  border-right-color: #ffa4f6;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Rules are in Alpbhectical Order */
* {
  box-sizing: border-box;
  font-family: museo-sans, sans-serif;
}

body {
  margin: 0;
}

/* show button outlines to users who use the tab key to navigate */
body:not(.user-is-tabbing) button:focus,
body:not(.user-is-tabbing) input:focus,
body:not(.user-is-tabbing) select:focus,
body:not(.user-is-tabbing) textarea:focus {
  outline: none;
}

button {
  height: 34px;
  border-radius: 1px;
  padding: 0 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

button > span {
  transition: color 0.3s ease-out;
}

